.codebar{
  width: 600px;
  background: #272822;
  position: relative;
}

.tab{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70px;
  padding: 0 15px;
  gap: 15px;
  border-bottom: 2px solid #333;
}

.item{
  display: flex;
  height: 50px;
  flex: 1 1 auto;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  background: #333;
  color: #fff;
  cursor: pointer;
  font-size: 15px;
  font-weight: 600;
  outline: none;
  border: none;
}

.activeTab{
  background: #c0a50e;
  color: #fff;
}

.editor{
  height: calc(100vh - 70px);
  padding: 10px;
}